import "./aineas.css";
import fight from "./../../../../../static/6..svg"
import leftClick from "./../../../../../static/leftClick.svg"
import React, { useCallback } from 'react'; 
import { useHistory } from "react-router-dom";
import CloseTroya from "../../../../shared/closeTroya/close";

export default function Aineas() {
  const history = useHistory()
  const left = useCallback(() => history.push("/troya/sahneler/hektor"), [history]);  
  const exit = useCallback(() => history.push("/troya/sahneler"), [history]);

  return (
    <div className="troyaBack">
      <div className="close-1">
        <CloseTroya onClick={exit}/>
      </div>
      <div className="image-container">
        <div className="click-up" onClick={left}>
          <img className="click-pass" src={leftClick} />
        </div>
        <div>
          <img class="portrait" src={fight}></img>
        </div>
      </div>
      <h3 className="tagHead">6. Aineas'ın Babası ve Oğluyla Troya'dan Kaçışı</h3>
      <h4 className="tagContent">''Yerleş boynumun üstüne sevgili baba, taşırım omuzlarımda seni, ağır gelmezsin bana.</h4>
      <h4 className="tagContent">Hepimiz ya birlikte gideriz ölüme, ya birlikte kurtuluruz ikimiz de, korkudan, arkamdan gelsin küçük ulus.'' </h4>
    </div>
  );
}
