import "./sohbet.css";
import sohbet from "./../../../../../static/4.p.svg"
import leftHand from "./../../../../../static/leftHand.svg"
import rightHand from "./../../../../../static/rightHand.svg"
import { useHistory } from "react-router-dom";
import React, { useCallback } from 'react'; 
import CloseButton from "../../../../shared/close/close";

export default function Sohbet() {

  const history = useHistory();
  const goToLeft = useCallback(() => history.push("/polyska/bezemeler/kutlama"), [history]);
  const goToRight = useCallback(() => history.push("/polyska/bezemeler/kurban"), [history]); 
  const exit = useCallback(() => history.push("/polyska"), [history]); 
  
  return (
    <div className="polyska-main">
      <div className="clButton">
      <CloseButton onClick={exit}/>
      </div>
      <div className="main-photos">
        <div onClick={goToLeft}>
          <img className="clicker" src={leftHand}></img>
        </div>
        <div className="middlePic">
        <img className="portrait" src={sohbet}></img>
        </div>
        <div onClick={goToRight}>
          <img className="clicker" src={rightHand}></img>
        </div>
      </div>
      <h3 className="tagHead">4. Sohbet Eden Kadınlar</h3>
      <h4 className="tagContent">Kısa kenarların diğerinde ise oturup sohbet eden kadınlar görülmektedir.</h4>
    </div>
  );
}
