import "./sahneler.css";
import Button from "./../../../shared/secondScreen/secondScreenButton";
import CloseTroya from "./../../../shared/closeTroya/close";
import React, { useCallback } from "react";
import { Link, useHistory } from "react-router-dom";

export default function Tarihcesi() {
  const history = useHistory();
  const goToAineas = useCallback(() => history.push("/troya/sahneler/aineas"), [
    history,
  ]);
  const goToAkha = useCallback(() => history.push("/troya/sahneler/akha"), [
    history,
  ]);
  const goToHektor = useCallback(() => history.push("/troya/sahneler/hektor"), [
    history,
  ]);
  const goToMenelaos = useCallback(
    () => history.push("/troya/sahneler/menelaos"),
    [history]
  );
  const goToPriamos = useCallback(
    () => history.push("/troya/sahneler/priamos"),
    [history]
  );
  const goToUlular = useCallback(() => history.push("/troya/sahneler/ulular"), [
    history,
  ]);
  const exit = useCallback(() => history.push("/troya"), [
    history,
  ]);

  const a = "17vh";
  const b = "17vh";

  return (
    <div className="troyaBack">
      <CloseTroya onClick={exit}/>
      <h3 className="troyaHead">TROYA SAVAŞI'NDAN SAHNELER</h3>
      <div className="sideby">
        <div className="btn-tr" style={{ padding: 15 }} onClick={goToAkha}>
          <Button
            text={"1.KARARGAHTA KAVGA"}
            backGroundColor={"#47858C"}
            height={a}
            width={b}
          />
        </div>
        <div className="btn-tr" style={{ padding: 15 }} onClick={goToUlular}>
          <Button
            text={"2. ULULARIN SEYRİ"}
            backGroundColor={"#47858C"}
            height={a}
            width={b}
          />
        </div>
      </div>
      <div className="sideby">
        <div className="btn-tr" style={{ padding: 15 }} onClick={goToMenelaos}>
          <Button
            text={"3. MELAOS VE PARİS"}
            backGroundColor={"#47858C"}
            height={a}
            width={b}
          />
        </div>
        <div className="btn-tr" style={{ padding: 15 }} onClick={goToPriamos}>
          <Button
            text={"4. PRİAMOS VE AKHILLEUS"}
            backGroundColor={"#47858C"}
            height={a}
            width={b}
          />
        </div>
      </div>

      <div className="sideby">
        <div className="btn-tr" style={{ padding: 15 }} onClick={goToHektor}>
          <Button
            text={"5. HEKTOR'UN CENAZESİ"}
            backGroundColor={"#47858C"}
            height={a}
            width={b}
          />
        </div>
        <div className="btn-tr" style={{ padding: 15 }} onClick={goToAineas}>
          <Button
            text={"6. AİNEAS'IN KAÇIŞI"}
            backGroundColor={"#47858C"}
            height={a}
            width={b}
          />
        </div>
      </div>
    </div>
  );
}
