import "./kurban.css";
import kurban from "./../../../../../static/1.p.svg"
import leftHand from "./../../../../../static/leftHand.svg"
import rightHand from "./../../../../../static/rightHand.svg"
import { useHistory } from "react-router-dom";
import React, { useCallback } from 'react'; 
import CloseButton from "../../../../shared/close/close";


export default function Kurban() {

  const history = useHistory();
  const goToLeft = useCallback(() => history.push("/polyska/bezemeler/sohbet"), [history]);
  const goToRight = useCallback(() => history.push("/polyska/bezemeler/hekabe"), [history]);
  const exit = useCallback(() => history.push("/polyska"), [history]); 
  

  return (
    <div className="polyska-main">
      <div className="closeButton">
      <CloseButton onClick={exit}/>
      </div>
      <div className="main-photos">
        <div onClick={goToLeft}>
          <img className="clicker" src={leftHand}></img>
        </div>
        <div className="middlePic">
        <img className="portrait" src={kurban}></img>
        </div>
        <div onClick={goToRight}>
          <img className="clicker" src={rightHand}></img>
        </div>
      </div>
      <h3 className="tagHead">1. Polyksena’nın Kurban Edilmesi</h3>
      <h4 className="tagContent">Uzun kenarlarından birinde, Troya Kralı Priamos ile kraliçe Hekabe’nin küçük kızları olan Polyksena’nın kurban edilmesi olayı betimlenmiştir. Bu nedenle eser, Polyksena Lahti olarak anılmaktadır.</h4>
    </div>
  );
}
