import "./App.css";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import { CSSTransition, TransitionGroup } from "react-transition-group";

import Main from "./components/screens/main/main.js";
import Troya from "./components/screens/troya/troya.js";
import Tarihcesi from "./components/screens/troya/tarihcesi/tarihcesi.js";

import Sahneler from "./components/screens/troya/sahneler/sahneler.js";
import Akha from "./components/screens/troya/sahneler/akha/akha.js";
import Ulular from "./components/screens/troya/sahneler/ulular/ulular.js";
import Menelaos from "./components/screens/troya/sahneler/menelaos/menelaos.js";
import Priamos from "./components/screens/troya/sahneler/priamos/priamos.js";
import Hektor from "./components/screens/troya/sahneler/hektor/hektor.js";
import Aineas from "./components/screens/troya/sahneler/aineas/aineas.js";

import Polyska from "./components/screens/polyska/polyska.js";
import Ptarihcesi from "./components/screens/polyska/tarihcesi/tarihcesi"
import Mitolojisi from "./components/screens/polyska/mitolojisi/mitolojisi"
import Kurban from "./components/screens/polyska/bezemeler/kurban/kurban"
import Hekabe from "./components/screens/polyska/bezemeler/hekabe/hekabe"
import Kutlama from "./components/screens/polyska/bezemeler/kutlama/kutlama"
import Sohbet from "./components/screens/polyska/bezemeler/sohbet/sohbet"

function App() {
  return (
    <div className="App">

    <TransitionGroup>
      <CSSTransition appear in classNames="model-transition" timeout={1000}>
        <Router>
          <Switch>
            <Route path="/muze" exact  component={Main}/>
            <Route path="/muze/troya" exact component={Troya}/>
            <Route path="/troya/sahneler" exact component={Sahneler}/>
            <Route path="/troya/sahneler/akha"  component={Akha}/>
            <Route path="/troya/sahneler/ulular"  component={Ulular}/>
            <Route path="/troya/sahneler/menelaos" component={Menelaos}/>
            <Route path="/troya/sahneler/priamos" component={Priamos}/>
            <Route path="/troya/sahneler/hektor" component={Hektor}/>
            <Route path="/troya/sahneler/aineas" component={Aineas}/>
            <Route path="/troya/tarihcesi" component={Tarihcesi}/>
            <Route path="/muze/polyska" exact component={Polyska}/>
            <Route path="/polyska/tarihcesi" component={Ptarihcesi}/>
            <Route path="/polyska/mitolojisi" component={Mitolojisi}/>
            <Route path="/polyska/bezemeler/kurban" component={Kurban}/>
            <Route path="/polyska/bezemeler/hekabe" component={Hekabe}/>
            <Route path="/polyska/bezemeler/kutlama" component={Kutlama}/>
            <Route path="/polyska/bezemeler/sohbet" component={Sohbet}/>
          </Switch>
        </Router>
      </CSSTransition>
    </TransitionGroup>
    </div>
  );
}

export default App;
