import "./main.css";
import React, { useCallback} from 'react';
import { useHistory } from "react-router-dom";
import polyksena from "./../../../static/POLYKSENA.svg";
import troya from "./../../../static/TROYA.svg";
import { Link } from "react-router-dom"
 
export default function Main() {
  const history = useHistory();
  const goToTroya = useCallback(() => history.push("/muze/troya"), [history]);
  const goToPolyska = useCallback(() => history.push("/muze/polyska"), [history]);

  
  return (
    <div className="main">
      <div className="main2">
        <div className="contentmain1">
          <div className="pictureDivs">
            <img src={polyksena} className="App-logo" alt="logo" />
          </div>
          <h1>POLYSENKA LAHTİ</h1>
            <div className="btn" onClick={goToPolyska}>
              Başla
            </div>
        </div>
        <div className="contentmain2">
          <div className="pictureDivs">
            <img src={troya} className="App-logo" alt="logo" />
          </div>
          <h1 class>TROYA SAVAŞI</h1>
          <div className="btn" onClick={goToTroya}>
            Başla
          </div>
        </div>
      </div>
    </div>
  );
}
