import "./tarihcesi.css";
import Button from "./../../../shared/secondScreen/secondScreenButton";
import photo from "./../../../../static/maviTarihçe.svg"
import React, { useCallback } from 'react'; 
import { Link, useHistory } from "react-router-dom";

import CloseTroya from "../../../shared/closeTroya/close";



export default function Tarihcesi() {

  const history = useHistory();
  const exit = useCallback(() => history.push("/troya"), [history]);

  return (
    <div className="troyaBack">
      <div className="troya-tarihce">
        <CloseTroya onClick={exit}/>
      </div>
      <div>
        <img className="portrait" src={photo}></img>
      </div>
      <h3 className="tagHead">Tarihçesi</h3>
      <h4 className="tagContent"> Troya Savaşı’nda Hellen Yarımadası’nın güneyinden gelen Akhalarla, Kuzeybatı Anadolu’da yerleşmiş olan Troyalılar çarpışırlar. Troya’nın müttefikleri Anadolu’nun 22 farklı bölgesinden Troya ordusuna katılır. Savaşı tanrılar yönlendirir; herbiri bir taraf tutar. Akhaların kumandanı Agamemnon’un ordusunu, Athena, Zeus’un karısı Tanrışa Hera ve Hephaistos Akhaların ordusunu destekler. Hektor’un öncülüğündeki Troyalıları Tanrı Apollon, Savaş Tanrısı Ares ve Tanrıça Aphrodite desteklemektedir. Zeus, uzun zaman Troyalıların yenilmesine izin vermez. Ama aradan geçen on yıl ve Tahta At hilesiyle kente giren Akha askerleri sonu belirler. </h4>
    </div>
  );
}
