import "./troya.css";
import React, { useCallback } from 'react'; 
import { Link, useHistory } from "react-router-dom";

import CloseTroya from "../../shared/closeTroya/close";
import Button from "./../../shared/secondScreen/secondScreenButton"


export default function Troya() {
  const history = useHistory();
  const goToTarihcesi = useCallback(() => history.push("/troya/tarihcesi"), [history]);  
  const goToSahneler = useCallback(() => history.push("/troya/sahneler"), [history]);  
  const exit = useCallback(() => history.push("/muze"), [history]);

  return (
    <div className="container-troya">
    <div className="troyaBack">
      <div className="troya-close">
        <CloseTroya onClick={exit} />
      </div>
      <div className="btn-tr" onClick={goToTarihcesi}>
      <Button text={"TARİHÇESİ"} backGroundColor={"#47858C"} height={"15vh"} width={"25vh"}/>
      </div>
      <div className="btn-tr" onClick={goToSahneler}>
        <Button text={"TROYA SAVAŞI'NDAN SAHNELER"} backGroundColor={"#47858C"} height={"15vh"} width={"25vh"}/>
      </div>
    </div>
    </div>
  );
}



