import "./kutlama.css";
import kutlama from "./../../../../../static/3.p.svg"
import leftHand from "./../../../../../static/leftHand.svg"
import rightHand from "./../../../../../static/rightHand.svg"
import { useHistory } from "react-router-dom";
import React, { useCallback } from 'react'; 
import CloseButton from "../../../../shared/close/close";

export default function Kutlama() {

  const history = useHistory();
  const goToLeft = useCallback(() => history.push("/polyska/bezemeler/hekabe"), [history]);
  const goToRight = useCallback(() => history.push("/polyska/bezemeler/sohbet"), [history]); 
  const exit = useCallback(() => history.push("/polyska"), [history]); 

  return (
    <div className="polyska-main">
      <div className="cloButton">
      <CloseButton onClick={exit}/>
      </div>
      <div className="main-photos">
        <div onClick={goToLeft}>
          <img className="clicker" src={leftHand}></img>
        </div>
        <div className="middlePic">
        <img className="portrait" src={kutlama}></img>
        </div>
        <div onClick={goToRight}>
          <img className="clicker" src={rightHand}></img>
        </div>
      </div>
      <h3 className="tagHead">3. Kutlama ve Hediyeler</h3>
      <h4 className="tagContent">Uzun kenarların bir diğerinde müzisyenler ve dansçıların eşlik ettiği bir topluluktan hediyeler alan tahta oturmuş bir kadın görülür.</h4>
    </div>
  );
}
