import "./tarihcesi.css";
import tarihce from "./../../../../static/tarihce.svg"
import CloseButton from "../../../shared/close/close";
import { useHistory } from "react-router-dom";
import React, { useCallback } from 'react'; 


export default function Tarihcesi() {

  const history = useHistory()
  const exit = useCallback(() => history.push("/polyska"), [history]); 

  return (
    <div className="polyska-main">
      <div className="csButton">
        <CloseButton onClick={exit}/>
      </div>
      <div>
        <img className="portrait" src={tarihce}></img>
      </div>
      <h3 className="tagHead">TARİHÇESİ</h3>
      <h4 className="tagContent">1994 yılında Granikos Havzası’nın doğusunda Gümüşçay Beldesi, Kızöldün Mevkii’nde bulunan ve MÖ 520-500 yıllarına tarihlenen bu lahi, Troas’ta Perslerin hakim olduğu döneme aittir. Anadolu’da bugüne kadar bulunan figür bezemeli lahitlerin en erken örneğidir.</h4>
      <h4 className="tagContent">Lahit üzerinde betimlenenlerin çoğu kadın olmasına rağmen, bu lahitte 40 yaşlarında bir erkek cesedi bulunmuştur.</h4>
      <h4 className="tagContent">İlk defa bir eser üzerinde birden fazla konuya yer verilmiştir.</h4>
    </div>
  );
}
