import "./akha.css";
import fight from "./../../../../../static/1..svg"
import rightClick from "./../../../../../static/rightClick.svg"
import React, { useCallback } from 'react'; 
import { useHistory } from "react-router-dom";
import CloseTroya from "../../../../shared/closeTroya/close";

export default function Akha() {
  const history = useHistory()
  const right = useCallback(() => history.push("/troya/sahneler/ulular"), [history]);  
  const exit = useCallback(() => history.push("/troya/sahneler"), [history]);

  return (
    <div className="troyaBack">
      <div className="close-1">
        <CloseTroya onClick={exit}/>
      </div>
      <div className="image-container">
        <div>
          <img class="portrait" src={fight}></img>
        </div>
        <div className="click-upper" onClick={right}>
          <img className="click-pass" src={rightClick} />
        </div>
      </div>
      <h3 className="tagHead">1. Akha Karargahında Agamemnon ve Akhilleus'un Kavgası</h3>
      <h4 className="tagContent">Agamemnon, savaş ganımeti Briseis'i Akhilleus'tan alır.</h4>
      <h4 className="tagContent">Akhilleus öfkeyle barakasına çekilir; savaşa artık katılmayacaktır.</h4>
    </div>
  );
}
