import "./hekabe.css";
import hekabe from "./../../../../../static/2.p.svg"
import leftHand from "./../../../../../static/leftHand.svg"
import rightHand from "./../../../../../static/rightHand.svg"
import { useHistory } from "react-router-dom";
import React, { useCallback } from 'react'; 
import CloseButton from "../../../../shared/close/close";

export default function Hekabe() {
  const history = useHistory();
  const goToLeft = useCallback(() => history.push("/polyska/bezemeler/kurban"), [history]);
  const goToRight = useCallback(() => history.push("/polyska/bezemeler/kutlama"), [history]); 
  const exit = useCallback(() => history.push("/polyska"), [history]); 

  return (
    <div className="polyska-main">
      <div className="closButton">
      <CloseButton onClick={exit}/>
      </div>
      <div className="main-photos">
        <div onClick={goToLeft}>
          <img className="clicker" src={leftHand}></img>
        </div>
        <div className="middlePic">
        <img className="portrait" src={hekabe}></img>
        </div>
        <div onClick={goToRight}>
          <img className="clicker" src={rightHand}></img>
        </div>
      </div>
      <h3 className="tagHead">2. Hekabe’nin Yası</h3>
      <h4 className="tagContent">Kısa kenarlardan birinde Polyksena’nın annesi Hekabe, yapraksız bir ağaç altında oturmuş yas tutar.</h4>
    </div>
  );
}
