import "./secondScreenButton.css";

function secondScreenButton(params) {
  const backGroundColor  = params.backGroundColor;
  const text = params.text;
  const height = params.height;
  const width = params.width; 

  return (
    <div className="secondScreenButton">
      <div style={{background: backGroundColor, height: height, width: width}} className="button" >
        <h2>{text}</h2>
      </div>
    </div>
  );
}

export default secondScreenButton;
