import "./polyska.css";
import Button from "./../../shared/secondScreen/secondScreenButton"
import CloseButton from "../../shared/close/close";
import { useHistory } from "react-router-dom";
import React, { useCallback } from 'react'; 

export default function Polyska() {
  const history = useHistory();
  const backToMain = useCallback(() => history.push("/muze"), [history])
  const goToTarihcesi = useCallback(() => history.push("/polyska/tarihcesi"), [history]); 
  const goToMitoloji = useCallback(() => history.push("/polyska/mitolojisi"), [history]);
  const goToBezemeler = useCallback(() => history.push("/polyska/bezemeler/kurban"), [history]);  


  return (
    <div className="polyska-main">
        <CloseButton onClick={backToMain}/>
        <div className="btn-tr" onClick={goToTarihcesi}>
        <Button text={"TARİHÇESİ"} height={"18vh"} width={"33vh"} backGroundColor={"#C66251"}/>
        </div>
        <div className="btn-tr" onClick={goToMitoloji}>
        <Button text={"MİTOLOJİSİ"} height={"18vh"} width={"33vh"} backGroundColor={"#C66251"}/>
        </div>
        <div className="btn-tr" onClick={goToBezemeler}>
        <Button text={"BEZEMELERİ"} height={"18vh"} width={"33vh"} backGroundColor={"#C66251"}/>
        </div>
    </div>
  );
}

