import "./mitolojisi.css";
import mitolojisi from "./../../../../static/mitolojisi.svg"
import CloseButton from "../../../shared/close/close";
import { useHistory } from "react-router-dom";
import React, { useCallback } from 'react'; 

export default function Mitolojisi() {
  const history = useHistory()
  const exit = useCallback(() => history.push("/polyska"), [history]); 


  return (
    <div className="polyska-main">
      <div className="cButton">
        <CloseButton onClick={exit}/>
      </div>
      <div>
        <img className="portrait" src={mitolojisi}></img>
      </div>
      <h3 className="tagHead">Mitolojisi</h3>
      <h4 className="tagContent">Polyksena Lahti, Troya Savaşı’nın mitolojisinin lahit üzerine işlenmiş en eski örneği olması açısından önemlidir.<br></br><br></br> Polyksena Lahti yüzeyindeki kabartmalar iki konu altında toplanır.<br></br><br></br> İlki mitolojik bir konudur: Troya Savaşı sonrası Troya Kralı Priamos’un kızı Polyksena’nın Akha kahramanı Aşil’in mezarı başında kurban edilişi ve Troyalı kadınların feryadı. <br></br><br></br> İkinci konu: Kadınların katıldığı bir kutlamadır.<br></br><br></br> Polyksena lahtinin yüzeyindeki kabartmalar iki konu altında toplanır. Her iki konunun merkezinde de kadınlar vardır.</h4>
    </div>
  );
}
