import "./ulular.css";
import fight from "./../../../../../static/2..svg"
import rightClick from "./../../../../../static/rightClick.svg"
import leftClick from "./../../../../../static/leftClick.svg"
import React, { useCallback } from 'react'; 
import { useHistory } from "react-router-dom";
import CloseTroya from "../../../../shared/closeTroya/close";

export default function Ulular() {
  const history = useHistory()
  const right = useCallback(() => history.push("/troya/sahneler/menelaos"), [history]);  
  const left = useCallback(() => history.push("/troya/sahneler/akha"), [history]);  
  const exit = useCallback(() => history.push("/troya/sahneler"), [history]);

  return (
    <div className="troyaBack">
      <div className="close-1">
        <CloseTroya onClick={exit}/>
      </div>
      <div className="image-container">
        <div className="click-upper" onClick={left}>
          <img className="click-pass" src={leftClick} />
        </div>
        <div>
          <img class="portrait" src={fight}></img>
        </div>
        <div className="click-upper" onClick={right}>
          <img className="click-pass" src={rightClick} />
        </div>
      </div>
      <h3 className="tagHead">2. Priamos ve Troya Ulularının Burçlardan Şavaşı Seyretmesi</h3>
      <h4 className="tagContent">Troya Kralı Priamos'la ihtiyarlar kurulu, surların üzerine dizilip savaşı seyrederler</h4>
      <h4 className="tagContent">Priamos Helene'den ovada carpışan Akhalı savaşçıları tanıtmasını ister.</h4>
    </div>
  );
}
