import "./hektor.css";
import fight from "./../../../../../static/5..svg"
import rightClick from "./../../../../../static/rightClick.svg"
import leftClick from "./../../../../../static/leftClick.svg"
import React, { useCallback } from 'react'; 
import { useHistory } from "react-router-dom";
import CloseTroya from "../../../../shared/closeTroya/close";

export default function Hektor() {
  const history = useHistory()
  const right = useCallback(() => history.push("/troya/sahneler/aineas"), [history]);  
  const left = useCallback(() => history.push("/troya/sahneler/priamos"), [history]);  
  const exit = useCallback(() => history.push("/troya/sahneler"), [history]);

  return (
    <div className="troyaBack">
      <div className="close-5">
        <CloseTroya onClick={exit}/>
      </div>
      <div className="image-container">
        <div className="click-upper" onClick={left}>
          <img className="click-pass" src={leftClick} />
        </div>
        <div>
          <img class="portrait" src={fight}></img>
        </div>
        <div className="click-upper" onClick={right}>
          <img className="click-pass" src={rightClick} />
        </div>
      </div>
      <h3 className="tagHead">5. Hektor'un Cenaze Töreni ve Mezarı</h3>
      <h4 className="tagContent">Hektor'a ağıtlar yakılır ve Zeusoğlu Kral Priamos'un sarayında cenaze töreni düzenlenir.</h4>
      <h4 className="tagContent">Bütün halk atları iyi süren Hektor'un ölüsü çevresinde toplanır ve Hektor için bir mezar tümseği yapar.</h4>
    </div>
  );
}
