import "./priamos.css";
import fight from "./../../../../../static/4..svg"
import rightClick from "./../../../../../static/rightClick.svg"
import leftClick from "./../../../../../static/leftClick.svg"
import React, { useCallback } from 'react'; 
import { useHistory } from "react-router-dom";
import CloseTroya from "../../../../shared/closeTroya/close";

export default function Priamos() {
  const history = useHistory()
  const right = useCallback(() => history.push("/troya/sahneler/hektor"), [history]);  
  const left = useCallback(() => history.push("/troya/sahneler/menelaos"), [history]);  
  const exit = useCallback(() => history.push("/troya/sahneler"), [history]);

  return (
    <div className="troyaBack">
      <div className="close-4">
        <CloseTroya onClick={exit}/>
      </div>
      <div className="image-container">
        <div className="click-upper" onClick={left}>
          <img className="click-pass" src={leftClick} />
        </div>
        <div>
          <img class="portrait" src={fight}></img>
        </div>
        <div className="click-upper" onClick={right}>
          <img className="click-pass" src={rightClick} />
        </div>
      </div>
      <h3 className="tagHead">4. Priamos'un Oğlu Hektor'un Ölüsünü Almak İçin Akhilleus'a Yalvarması</h3>
      <h4 className="tagContent">Kral Priamos, oğlu Hektor'un ölüsünü geri alabilmek için Akhilleus'un baraksına gelip yalvarır.</h4>
    </div>
  );
}
