import "./menelaos.css";
import fight from "./../../../../../static/3..svg"
import rightClick from "./../../../../../static/rightClick.svg"
import leftClick from "./../../../../../static/leftClick.svg"
import React, { useCallback } from 'react'; 
import { useHistory } from "react-router-dom";
import CloseTroya from "../../../../shared/closeTroya/close";

export default function Menelaos() {
  const history = useHistory()
  const right = useCallback(() => history.push("/troya/sahneler/priamos"), [history]);  
  const left = useCallback(() => history.push("/troya/sahneler/ulular"), [history]);  
  const exit = useCallback(() => history.push("/troya/sahneler"), [history]);

  return (
    <div className="troyaBack">
      <div className="close-1">
        <CloseTroya onClick={exit}/>
      </div>
      <div className="image-container">
        <div className="click-upper" onClick={left}>
          <img className="click-pass" src={leftClick} />
        </div>
        <div>
          <img class="portrait" src={fight}></img>
        </div>
        <div className="click-upper" onClick={right}>
          <img className="click-pass" src={rightClick} />
        </div>
      </div>
      <h3 className="tagHead">3. Menelaos ve Paris'in Çarpışması</h3>
      <h4 className="tagContent">
         İki ordu karşı karşıyadır; <br></br> 
        Paris (Aleksandros), Helene'nin eski kocası Menelaos ile teke tek dövüşe girişmeyi önerir.</h4>
      <h4 className="tagContent">Dövüşü kazanan Helene'yi alacaktır.</h4>
    </div>
  );
}
